@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,900&display=swap');

*
{
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;

}

.App
{
    margin: 0;
    padding: 0;
}

.App-Header
{
    margin: 10px;
    padding: 10px;
    background-color: black;
    height: 100px;
    border: 0px solid #000;
    border-radius: 10px;
}

.Data
{
    /* height: 100vh;
    width: 100%; */
}

h1
{
    font-size: 40px;
    color: #fff;
}

.Details
{
    margin: 40px;
    padding: 20px;
    height: fit-content;
    width: 300px;
    border: 2px solid #000;
    border-radius: 10px;
    text-align: center;
}

.pictures
{
    height: 50%;
    width: 30%;
    border: 2px solid #000;
    border-radius: 10px;
}

.title
{
    font-size: 30px;
}